.burger {
	display: none;
	cursor: pointer;
	align-items: center;
	width: 35px;
	height: 20px;

	span {
		position: relative;
		display: inline-block;
		width: 35px;
		height: 2px;
		vertical-align: bottom;
		background: #fff;
		transition: background .2s .4s;

		&:before, &:after {
			content: "";
			position: absolute;
			background: #fff;
			transform-origin: 50% 50%;
			transition: top .3s .3s,transform .3s;
			width: 100%;
			height: 2px;
			left: 0; }

		&:before {
			top: 8px; }

		&:after {
			top: -8px; } } }

.js__menu-open {
	.burger > span {
		background: 0 0;
		&:before {
			transform: rotate3d(0, 0, 1, 45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; }
		&:after {
			transform: rotate3d(0, 0, 1, -45deg);
			top: 0;
			transition: top .3s,transform .3s .5s; } } }

