			width: 50%;

			&:first-child {
				padding-right: 7px; }

			&:last-child {
				padding-left: 7px; } } } }

.tovar-type {
	display: flex;

	&__item {
		width: 50%; }

	&__input {
		display: none;

		&:checked {

			+ label {
				background: #f1f1f1; } } }

	&__label {
		display: block;
		border: 1px solid $border-color;
		transition: background .3s ease;
		text-align: center;
		height: 38px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		user-select: none; } }


.counter {

	&__input {
		border: 1px solid $border-color;
		text-align: center;
		font-weight: bold;
		font-size: rem(18);
		height: 38px;
		width: 100%; } }

.tovar-price {
	font-weight: bold;
	white-space: nowrap; }

.tovar-desc {

	&__table {
		width: 100%;

		td {
			padding: 6px 20px; }

		tr {

			&:nth-child(odd) {

				td {
					background: #f2f2f2; } } } } }

