	.head-logo {
		margin-left: 0; }

	.callback {
		display: none; }

	.mobile-menu {
		.callback {
			display: block;

			a {
				color: #000;
				padding: 15px;
				text-decoration: none;
				display: block;
				border-bottom: 1px solid #eee; } } }



	.promo-video-cont {
