		&-forms {
			margin-top: 20px; } }

	.map {
		margin-top: 45px; }








	.page-promo {

		#content {
			background: none !important; }

		.bread-crumbs {
			justify-content: flex-start;

			&__link, &__this {
				color: #000; } }


		.title-block__title {
			color: #000;
			text-align: left; }


		.head- {
			background: $main-color; } }



	.promo__item {
		&-cont {
			flex-direction: column;
			padding: 1.4rem 0; }

		&:nth-child(odd) {
			.promo__item {
				&-cont {
