		&-img {
			margin-bottom: 1.4rem !important; } }

	.object {
		&__list {
			margin: 0; }

		&__item {
			width: 100%;
			margin: 0 0 30px 00; } }

	.equipment {
		&__list {
			margin: 0; }

		&__item {
			width: 100%;
			margin: 0 0 30px 0; }

		&-title {
			margin-bottom: 10px; } }

	.sale {
		&__list {
			margin: 0; }
		&__item {
			width: 100%;
			margin: 0 0 30px 0;

			img {
				width: 100%;
				object-fit: cover;
				object-position: center; } } }

	.category {
		&__list {
			margin: 0; }

		&__item {
			width: 100%;
			margin: 0 0 30px;

			&-title {
				margin-bottom: 15px; } } }

	.paginator {
		margin-top: 20px; }

	.image--right {
		margin-left: 0;
		max-width: 100%;
		float: none; }

	.inner {
