			line-height: 1.3rem; }

		h4 {
			font-size: 1rem;
			line-height: 1.3rem; } }

	.bread-crumbs {
		&__link:after {
			margin-left: 8px;
			padding-right: 10px; }

		&__this {
 } }			// display: none

	.main- {
		&prod_item {
			width: 100%;

			&:after {
				left: 20px; } } }


	.main {
		&-prod {
			&_block {
				margin: 0 0 -30px 0; }

			&_item {
				margin: 0 0 30px 0; } }

		&-slider_info_title {
			font-size: 1.5rem; }

		&-slide {
